import React, { Component } from 'react';
import Img from '../Img';
import Anchor from '../Anchor';

import './team.css';
import cezar from '../../assets/img/cezar.jpg';
import ondrej from '../../assets/img/ondrej.jpg';
import marius from '../../assets/img/marius.jpg';

import { Grid, Row, Col } from 'react-bootstrap';
export default class Team extends Component {
  render() {
    return (
      <div id="teamCss" className="section dark">
        <Anchor id="team" />
        <Grid>
          <Row>
            <Col md={12} sm={12} xs={12} >
              <h2 className="title">Team</h2>
            </Col>
            <Col mdOffset={2} smOffset={2} md={8} sm={8} xs={12} >
              <p className="description">We are constantly looking for self improvement and learning opportunities. We experiment every day with new technologies for front and backend development. We love that our job is to turn our experiments into real life projects and happy customers.</p>
            </Col>
          </Row>
          <Row>
            <Col md={4} sm={4} xs={12}>
              <div className="wrapper">
                <a href="#" className="portrait-wrapper">
                  <Img className="portrait" src={cezar} alt="Cezar Neaga"/>
                </a>
                <div className="text-center">
                  <h3>Cezar Neaga</h3>
                  <p>User Experience Design</p>
                </div>
              </div>
            </Col>
            <Col md={4} sm={4} xs={12}>
              <div className="wrapper">
                <a href="#" className="portrait-wrapper">
                  <Img className="portrait" src={marius} alt="Marius Marinescu" />
                </a>
                <div className="text-center">
                  <h3>Marius Marinescu</h3>
                  <p>Senior Developer</p>
                </div>
              </div>
            </Col>
            <Col md={4} sm={4} xs={12}>
              <div className="wrapper">
                <a href="#" className="portrait-wrapper">
                  <Img className="portrait" src={ondrej} alt="Ondrej Gandel" />
                </a>
                <div className="text-center">
                  <h3>Ondrej Gandel</h3>
                  <p>Content Marketing</p>
                </div>
              </div>
            </Col>
           </Row>
        </Grid>
      </div>
    );
  }
}



