import React, { Component } from 'react';
import Img from '../Img';
import { Grid, Row, Col } from 'react-bootstrap';
import Slider from 'react-slick';
import Anchor from '../Anchor';
import './clients.css';
import mhpss from '../../assets/img/clients/mhpss.png';
import sia from '../../assets/img/clients/sia.png';
import unicef from '../../assets/img/clients/unicef.png';
import hub from '../../assets/img/clients/hub.png';
import ifrc from '../../assets/img/clients/ifrc.png';
import zero from '../../assets/img/clients/zero.png';
import inventures from '../../assets/img/clients/inventures.png';

export default class Clients extends Component {
  render() {
    const settings = {
      autoplay: true,
      arrows: false,
      pauseOnHover: true,
      // className: 'center',
      centerMode: true,
      initialSlide: 2,
      speed: 500,
      infinite: true,
      slidesToShow: 5,
      slidesToScroll: 1,
      responsive: [ { breakpoint: 768, settings: { slidesToShow: 3 } }, { breakpoint: 1024, settings: { slidesToShow: 4 } }, { breakpoint: 420, settings: { slidesToShow: 2 } } ]
    };
    return (
      <div id="clientsCss" className="section">
      <Anchor id="clients" />
        <Grid>
          <Row>
            <Col md={12} sm={12} xs={12} >
              <h2 className="title">Clients</h2>
            </Col>
            <Col mdOffset={2} smOffset={2} md={8} sm={8} xs={12} >
              <p className="description">Over the past 7 years we have helped countless <span className="highlight">individuals and organisations</span> bring their ideas to the world. From entrepreneurs to international development agencies, we dedicate our enthusiasm to meaningful projects that are making the world a better place.
              </p>
            </Col>
          </Row>
          <Row>
            <Col mdOffset={1} smOffset={1} md={10} sm={10} xs={12} >
              <Slider {...settings}>

                <div>
                  <a href="http://mhpss.net" className="thumbnail">
                    <Img src={mhpss} alt="MHPSS" />
                  </a>
                </div>
                <div>
                  <a href="http://socialimpactaward.net" className="thumbnail">
                    <Img src={sia} alt="Social Impact Award" />
                  </a>
                </div>
                <div>
                  <a href="http://unicef.org" className="thumbnail">
                    <Img src={unicef} alt="UNICEF" />
                  </a>
                </div>
                <div>
                  <a href="http://impacthub.net" className="thumbnail">
                    <Img src={hub} alt="Impact Hub" />
                  </a>
                </div>
                <div>
                  <a href="http://pscentre.org/" className="thumbnail">
                    <Img src={ifrc} alt="IFRC - Psychosocial Centre" />
                  </a>
                </div>
                <div>
                  <a href="http://zeroproject.org" className="thumbnail">
                    <Img src={zero} alt="Zero Project" />
                  </a>
                </div>
                <div>
                  <a href="#" className="thumbnail">
                    <Img src={inventures} alt="Inventures" />
                  </a>
                </div>
              </Slider>
            </Col>
          </Row>
        </Grid>
      </div>
    );
  }
}



