import React, { PropTypes } from 'react';
const Anchor = (props) => (
  <a id={props.id} href={'#' + props.id}>
  </a>
)
Anchor.propTypes = {
  id: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ])
};
export default Anchor;



