import React, { Component } from 'react';
import { GoogleMapLoader, GoogleMap, InfoWindow, Marker } from "react-google-maps";
import { Grid, Row, Col } from 'react-bootstrap';
import Anchor from '../Anchor';

import './contact.css';
export default class Contact extends Component {
  state = {
    center: {
      lat: 48.1995264,
      lng: 16.3458338,
    },
    markers: [
      {
        // eslint-disable-next-line
        position: new google.maps.LatLng(48.1995264, 16.3458338),
        showInfo: false
      }
    ]
  }
  handleMarkerClick(marker) {
    marker.showInfo = true;
    this.setState(this.state);
  }

  handleMarkerClose(marker) {
    marker.showInfo = false;
    this.setState(this.state);
  }

  renderInfoWindow(marker) {

    return (

      //You can nest components inside of InfoWindow!
      <InfoWindow
        key='marker_0_info_window'
        onCloseclick={this.handleMarkerClose.bind(this, marker)} >
        <div className="popout">
          <svg id="Layer_1"
            width="32" height="32" viewBox="0 0 32 32">
            <path d="M32 14h-3.154c-0.864-5.57-5.276-9.982-10.846-10.846v-3.154h-4v3.154c-5.57 0.864-9.982 5.276-10.846 10.846h-3.154v4h3.154c0.864 5.57 5.276 9.982 10.846 10.846v3.154h4v-3.154c5.57-0.864 9.982-5.276 10.846-10.846h3.154v-4zM24.776 14h-3.118c-0.603-1.705-1.953-3.056-3.658-3.658v-3.118c3.36 0.765 6.010 3.416 6.776 6.776zM16 18c-1.105 0-2-0.895-2-2s0.895-2 2-2c1.105 0 2 0.895 2 2s-0.895 2-2 2zM14 7.224v3.118c-1.705 0.603-3.056 1.953-3.658 3.658h-3.118c0.765-3.36 3.416-6.010 6.776-6.776zM7.224 18h3.118c0.603 1.705 1.953 3.056 3.658 3.658v3.118c-3.36-0.765-6.010-3.416-6.776-6.776zM18 24.776v-3.118c1.705-0.603 3.056-1.953 3.658-3.658h3.118c-0.765 3.36-3.416 6.010-6.776 6.776z"></path>
          </svg>
          <p>If you are around, lets have a coffee or go for a meal in the neighbourhood. It's packed with restaurants and coffee bars.</p>
        </div>
      </InfoWindow>

    );

  }
  render() {
    return (
      <div id="contactCss" className="section footer-padding">
        <Anchor id="contact" />
        <Grid>
          <Row>
            <Col md={12} sm={12} xs={12} >
              <h2 className="title">Get in touch!</h2>
            </Col>
            <Col mdOffset={2} smOffset={2} md={8} sm={8} xs={12} >
              <p className="description">
                We are always glad to hear from you! If you have any <span className="highlight">questions</span>, need a bit of <span className="highlight">advice</span> with your web project or would like to just <span className="highlight">meet</span>, drop us an email. We reply to all emails!
              </p>
            </Col>
          </Row>
        </Grid>
        <GoogleMapLoader
          containerElement={
            <div
              {...this.props}
              style={{
                height: '400px'
              }}
            >
            </div>
          }
          googleMapElement={
            <GoogleMap
              center={this.state.center}
              defaultZoom={16}
              options={ {scrollwheel: false} }
              ref='map'>

              {this.state.markers.map((marker, index) =>
                {
                  return (
                    <Marker
                      key={index}
                      ref='marker_0'
                      position={marker.position}
                      onClick={this.handleMarkerClick.bind(this, marker)} >
                      {marker.showInfo ? this.renderInfoWindow( marker) : null}
                    </Marker>
                  );
                })
              }
            </GoogleMap>
          }
        />
        <Grid className="sm-pad">
          <Row>
            <Col md={4} sm={4} xs={12}>
              <p className="service">
                <span className="icon-wrapper">
                  <span className="circle"><i className="fa fa-map-marker"></i></span>
                </span>
                Lindengasse 56, 1070, Vienna, Austria
              </p>
            </Col>
            <Col md={4} sm={4} xs={12}>
              <p className="service">
                <span className="icon-wrapper">
                  <span className="circle"><i className="fa fa-phone"></i></span>
                </span>
                +43 676 4445906
              </p>
            </Col>
            <Col md={4} sm={4} xs={12} >
              <p className="service">
                <span className="icon-wrapper">
                  <span className="circle"><i className="fa fa-envelope"></i></span>
                </span>
                hi@cezarneaga.eu
              </p>
            </Col>
          </Row>
        </Grid>
      </div>
    );
  }
}



