import React, { Component } from 'react';
import Waypoint from 'react-waypoint';

import Hero from './components/Hero';
import Img from './components/Img';
import navlogo from './assets/img/nav-logo.png';

import { Navbar, Nav, NavItem } from 'react-bootstrap';

import Services from './components/Services';
import Clients from './components/Clients';
import Team from './components/Team';
import Contact from './components/Contact';
import Footer from './components/Footer';

import './App.css';
import './components/Navi/navi.css';
import { StickyContainer, Sticky } from 'react-sticky';

const sections = {
  services: '#services',
  clients: '#clients',
  team: '#team',
  contact: '#contact',
}

let ScrollSpy = ({ href, onBefore, onAfter }) => (
  <Waypoint
    onEnter={({ previousPosition }) => (
      previousPosition === Waypoint.above && onBefore(href)
    )}
    onLeave={({ currentPosition }) => (
      currentPosition === Waypoint.above && onAfter(href)
    )}
    topOffset={20}
    bottomOffset={-20}
  />
);

class App extends Component {
  constructor(props) {
    super(props)
    this.state = { activeNavItemHref: null }
    this.onBefore = this.onBefore.bind(this)
    this.onAfter = this.onAfter.bind(this)
    this.setActiveNavItem = this.setActiveNavItem.bind(this)
    this.updateActiveHref = this.updateActiveHref.bind(this)
    this.renderScrollSpy = this.renderScrollSpy.bind(this)
    this.handleNavItemSelect = this.handleNavItemSelect.bind(this)
  }

  handleNavItemSelect(key, e) {
    window.location = e.target.href;
    //this.toggleExpanded(false);
  }
  // toggleExpanded(expanded){
  //   this.setState({
  //     expanded: expanded
  //   })
  // }

  componentDidMount() {
    this.afterSections = {};
    Object.keys(sections).forEach(
      // eslint-disable-next-line
      key => this.afterSections[sections[key]] = false
    );
    const { hash } = window.location;

    if (this.afterSections[hash] !== undefined) {
      for (let href of Object.keys(this.afterSections)) {
        this.afterSections[href] = true;

        if (href === hash) {
          this.setActiveNavItem();
          break;
        }
      }
    }
  }

  setActiveNavItem() {
    let activeNavItemHref = null;

    for (let href of Object.keys(this.afterSections)) {
      if (this.afterSections[href]) {
        activeNavItemHref = href;
      }
    }
    this.setState({ activeNavItemHref });
  }

  renderScrollSpy(href) {
    return (
      <ScrollSpy
        href={href}
        onBefore={this.onBefore}
        onAfter={this.onAfter}
      />
    );
  }

  onBefore(href) {
    this.afterSections[href] = false;
    this.updateActiveHref();
  }

  onAfter(href) {
    this.afterSections[href] = true;
    this.updateActiveHref();
  }

  updateActiveHref() {
    let updateActiveHrefHandle
    if (updateActiveHrefHandle != null) {
      return;
    }

    updateActiveHrefHandle = setTimeout(() => {
      updateActiveHrefHandle = null;
      this.setActiveNavItem();
    });
  }
  render() {
    return (
      <div>
        <StickyContainer>
          <Hero />
          <Sticky>
          <Navbar
            inverse
            //expanded={this.state.expanded}
            //onToggle={this.toggleExpanded}
            >
            <Navbar.Header>
              <Navbar.Brand>
                <a href="#home">
                  <Img src={navlogo} alt="Welcome" />
                </a>
              </Navbar.Brand>
              <Navbar.Toggle />
            </Navbar.Header>
            <Navbar.Collapse>
              <Nav
                pullRight
                activeHref={this.state.activeNavItemHref}
                onSelect={this.handleNavItemSelect} >
                <NavItem eventKey={1} href={sections.services}>SERVICES</NavItem>
                <NavItem eventKey={2} href={sections.clients}>CLIENTS</NavItem>
                <NavItem eventKey={3} href={sections.team}>TEAM</NavItem>
                <NavItem eventKey={4} href={sections.contact}>CONTACT</NavItem>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          </Sticky>
          {this.renderScrollSpy(sections.services)}
          <Services />
          {this.renderScrollSpy(sections.clients)}
          <Clients />
          {this.renderScrollSpy(sections.team)}
          <Team />
          {this.renderScrollSpy(sections.contact)}
          <Contact />
          <Footer />
        </StickyContainer>
      </div>
    );
  }
}
export default App;



